.button {
  background-color: transparent;
  border: #1ad1b9 2px solid;
  color: #1ad1b9;
}

.button:hover,
.button:active {
  color: white;
  border: #0285ff 2px solid;
}

.button span {
  margin: 0 0.5rem;
}

.badge {
  background-color: #1fceb6;
  border-radius: 30px;
  padding: 0.15rem 1.25rem;
  color: #1d1d1d;
}

.badge:hover{
  color: #f5fdfc;
}